* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

.container {
  max-width: 1300px;
  margin: 0 auto;
  padding: 0px 15px;
}

.cont {
  max-width: 800px;
  margin: 0 auto;
  height: 90vh;
}

code {
  word-break: break-word;
  word-wrap: break-word;
  padding: 1px 4px 2px;
  font-size: inherit;
  border-width: 1px;
  border-style: solid;
  border-radius: 4px;
  border-color: #d5d5d5;
}

button {
  width: 100px;
  height: 30px;
  color: #1a1a1a;
  background: transparent;
  border: 1px solid black;
  border-radius: 5px;
  display: block;
  margin: 20px 1px;
  cursor: pointer;
}

button:hover {
  box-shadow: inset 10px 0 10px -5px rgba(0, 0, 0, 0.5), inset -10px 0 10px -5px rgba(0, 0, 0, 0.5);
}

.row {
  width: 100%;
  display: flex;
}

nav {
  background-color: #1B2B34;
}

.nav {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
}

.nav-menu ul {
  list-style-type: none;
  display: inline-flex;
}

.nav-menu ul li {
  width: 110px;
  height: 45px;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  text-align: center;
}

.nav-menu ul li a {
  text-decoration: none;
  color: white;
  font-size: 18px;
  font-weight: 500;
  margin-top: 8px;
  display: block;
}

.nav-n {
  flex: 0 0 60%;
}

.nav-n ul {
  list-style-type: none;
  display: inline-flex;
}

.dropdown-menu {
  width: 330px;
  margin-right: 20px;
}

i {
  font-size: 25px;
  margin-right: 12px;
}

.select-btn {
  display: flex;
  height: 45px;
  background: transparent;
  padding: 20px;
  font-size: 18px;
  font-weight: 400;
  align-items: center;
  cursor: pointer;
  justify-content: space-between;
  border-left: 1px solid #000000;
  border-right: 1px solid #000000;
  color: white;
}

.options {
  position: absolute;
  width: 330px;
  overflow-y: auto;
  height: auto;
  padding: 4px;
  animation-name: fadeInDown;
  animation-duration: 0.35s;
  animation-fill-mode: both;
  display: none;
}

.option {
  display: flex;
  height: 45px;
  cursor: pointer;
  padding: 0 16px;
  align-items: center;
  background: #273E4B;
  box-shadow: 0 0 10px rgba(18, 229, 18, 0.2);
  border-bottom: 1px solid rgba(59, 31, 20, 0.7);
}

.option span {
  color: white;
}

.option:hover {
  background: #343D42;
}

.select-btn i {
  font-size: 25px;
  transition: 0.3s;
}

.dropdown-menu.active .select-btn i {
  transform: rotate(-180deg);
}

.dropdown-menu.active .options {
  display: block;
  opacity: 0;
  z-index: 10;
  animation-name: fadeInUp;
  animation-duration: 0.4s;
  animation-fill-mode: both;
}

@keyframes fadeInUp {
  from {
    transform: translate3d(0, 30px, 0);
    opacity: 0;
  }

  to {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }
}

@keyframes fadeInDown {
  from {
    transform: translate3d(0, 0, 0);
    opacity: 1;
  }

  to {
    transform: translate3d(0, 20px, 0);
    opacity: 0;
  }
}

.present-slider {
  position: relative;
  width: 100%;
  height: auto;
}

.present-slider-btn {
  width: 50px;
  height: 40px;
  font-size: 30px;
  color: rgb(237, 233, 241);
}

.present-slider-btn:hover {
